import { DatatableRecord } from "@/components/VDatatable/index.types";

export const groupRecords = (
  records: DatatableRecord[],
  groupBy: string
): DatatableRecord[] => {
  const groups = records.reduce((acc, record) => {
    const key = record[groupBy];
    acc[key] = (acc[key] || []).concat(record);

    return acc;
  }, {} as { [key: string]: DatatableRecord[] });

  return Object.values(groups).reduce((acc, group) => acc.concat(group), []);
};
