<template>
  <table>
    <th v-for="{ id, title } of columns" :key="id">
      {{ title }}
    </th>
    <tr v-for="record of records" :key="record.id">
      <td v-for="{ id, key } of columns" :key="id">{{ record[key] }}</td>
    </tr>
  </table>
</template>

<script lang="ts">
import { defineComponent, PropType } from "vue";
import { DatatableColumn, DatatableRecord } from "./index.types";

export default defineComponent({
  name: "VDatatable",
  props: {
    records: {
      type: Array as PropType<DatatableRecord[]>,
      default: () => [],
    },
    columns: {
      type: Array as PropType<DatatableColumn[]>,
      default: () => [],
    },
  },
});
</script>

