<template>
  <v-datatable :columns="datatableCols" :records="records" />
</template>

<script lang="ts">
import { defineComponent } from "vue";
import { VDatatable } from "@/components";
import { API } from "@/mock";

import {
  DatatableColumn,
  DatatableRecord,
} from "./components/VDatatable/index.types";

const datatableCols: DatatableColumn[] = [
  { id: 1, key: "company", title: "Company" },
  { id: 2, key: "contact", title: "Contact" },
  { id: 3, key: "email", title: "Email" },
  { id: 4, key: "phone", title: "Phone" },
  { id: 5, key: "country", title: "Country" },
  { id: 6, key: "city", title: "City" },
  { id: 7, key: "street", title: "Street" },
];

export default defineComponent({
  name: "App",
  components: { VDatatable },
  data: () => ({
    records: [] as DatatableRecord[],
    datatableCols,
  }),
  async mounted() {
    this.records = (await API.load()).records;
  },
});
</script>

